import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { claimSettingsMenus, menuItem } from 'src/app/model/menu';
import { SettingsService } from 'src/app/services/settings/settings.service';

@Component({
  selector: 'app-claim-settings-layout',
  templateUrl: './claim-settings-layout.component.html',
  styleUrls: [ './claim-settings-layout.component.scss' ]
})
export class ClaimSettingsLayoutComponent {
  menuItems = claimSettingsMenus;
  /**
   * constructor
   * @param router 
   */
  constructor(
    private router: Router,
  ) { }

  /**
   * on init 
   */
  ngOnInit(): void {
    console.log('ngOnInit');
  }

  /**
   * navigate to selected route
   * @param item 
   */
  navigate(item: menuItem): void {
    const url = item.url;
    {
      this.router.navigate([ url ]);
    }
  }
}
