import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Route, RouterModule } from '@angular/router';
import { SharedModule } from 'src/app/shared/shared.module';
import { ClaimSettingsLayoutComponent } from './settings-layout/claim-settings-layout/claim-settings-layout.component';

const claimRoutes: Route[] = [
  {
    path: '',
    component: ClaimSettingsLayoutComponent,
    loadChildren: () =>
      import('../claim-settings/settings-layout/claim-settings-layout.module').then(
        m => m.ClaimSettingsLayoutModule
      )
  }
];

@NgModule({
  declarations: [
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(claimRoutes),
    SharedModule
  ]
})
export class ClaimSettingsModule { }
